.repositories_not_found_container {
  background: #f9f9f9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-area: repos;
  height: 90vh;


.repositories_not_found_block {
  position: relative;
  bottom: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .repositories_not_found_img {
    max-width: 110px;
    max-height: 110px;
    margin-bottom: 24px;
  }

  .repositories_not_found_text {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 140%;
    color: #808080;
    text-align: center;
  }
}
}